}

#quote-body {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  color: black;
  background-color: #555; }

#quote-box {
  font-family: 'Indie Flower', cursive;
  background-color: white;
  background: -webkit-repeating-linear-gradient(#fff, #fff 25px, #9198e5 26px, #9198e5 27px);
  background: repeating-linear-gradient(#fff, #fff 25px, #9198e5 26px, #9198e5 27px);
  background-position-y: 34px;
  height: 300px;
  width: 500px;
  padding: 0;
  margin-top: 50px; }

#quote-header {
  position: relative;
  background: -webkit-linear-gradient(#fff, #fff 33px, pink 35px, pink 36px);
  background: linear-gradient(#fff, #fff 33px, pink 35px, pink 36px);
  height: 36px; }
  #quote-header #author {
    position: absolute;
    left: 10px;
    top: 5px;
    font-size: 30px;
    font-weight: 700; }
  #quote-header #author::after {
    content: " said:";
    font-size: 20px; }
  #quote-header #tweet-quote {
    position: absolute;
    top: 4px;
    right: 45px;
    color: #555;
    font-size: 25px; }
  #quote-header #tweet-quote:hover {
    color: #1da1f2; }
  #quote-header #new-quote {
    position: absolute;
    top: 6px;
    right: 10px;
    background: none;
    border: none;
    padding: 0;
    color: #555;
    font-size: 22px; }
  #quote-header #new-quote:hover {
    color: #ff0055;
    cursor: pointer; }

#text {
  position: relative;
  top: 10px;
  font-size: 22px;
  padding: 0 20px;
  line-height: 27px; }

#text::before {
  content: "\""; }

#text::after {
  content: "\""; }

.rotate {
  -webkit-transform: rotate(12deg);
          transform: rotate(12deg); }

.rotate:hover {
  -webkit-transform: rotate(-12deg);
          transform: rotate(-12deg); }

#md-prev-body * {
  box-sizing: border-box;
  scrollbar-width: thin; }

#md-prev-body {
  position: relative;
  color: black;
  width: 100%;
  height: 100%; }

#md-title {
  margin: 0; }
  #md-title span {
    font-size: 125px;
    position: absolute;
    top: -25px; }

#md {
  left: 0px;
  font-family: Courier, monospace; }

#previewer {
  font-family: "Satisfy", "Lucida Handwriting", cursive;
  right: 5px; }

#container {
  position: absolute;
  top: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-areas: "editor preview";
  grid-template-columns: auto 1fr; }

#editor {
  grid-area: editor;
  height: 100%;
  width: 45vw;
  padding: 90px 10px 10px 10px;
  margin: 0;
  border: none;
  background-color: rgba(158, 214, 190, 0.75);
  resize: horizontal; }

#preview {
  font-family: Arial, Helvetica, sans-serif;
  grid-area: preview;
  background-color: rgba(158, 182, 214, 0.75);
  width: 100%;
  height: 100%;
  padding: 90px 10px 10px 10px;
  overflow-y: scroll;
  overflow-x: hidden; }
  #preview img {
    max-width: 100%;
    height: auto; }

@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff") format("woff"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7") format("svg"); }

#pomodoro-body {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: grid;
  place-items: start center;
  background-color: #03256C;
  color: white;
  font-family: Comfortaa, sans-serif;
  position: relative; }

h1 {
  font-size: 50px; }

.time-text {
  font-family: digital, monospace; }

button {
  background-color: #2541B2;
  border: none;
  color: white; }

#timer-display {
  display: grid;
  place-items: center; }

#start_stop, #reset {
  padding: 10px;
  border-radius: 5px;
  margin-left: 5px; }

#timer-label {
  font-size: 30px; }

#timer-label, #session-label, #break-label {
  text-transform: capitalize; }

#time-left {
  font-size: 108px;
  background-color: #1768AC;
  padding: 10px 20px;
  margin: 5px 0 10px 0;
  border-radius: 10px; }

#settings {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 60px;
  background-color: #2541B2;
  display: flex;
  justify-content: center; }

.setTimer {
  font-size: 30px;
  display: inline-block;
  position: relative;
  margin: 5px 20px; }
  .setTimer .time-text {
    font-size: 40px; }
  .setTimer button {
    font-size: 20px;
    position: relative;
    width: 30px;
    height: 20px; }
  .setTimer .incr {
    top: -14px;
    left: 5px; }
  .setTimer .decr {
    top: 5px;
    left: -25px; }

#main-nav {
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0 20px 7px 20px;
  width: 100vw;
  background-color: rgba(35, 39, 42, 0.8); }
  #main-nav ul {
    list-style: none;
    display: flex;
    padding: 0px;
    margin: 0px; }
  #main-nav li {
    display: inline-block;
    margin: 0px;
    padding: 0px; }
  #main-nav a {
    display: inline-block;
    text-decoration: none;
    background: #23272a;
    color: #86c332;
    padding: 10px 20px;
    margin-right: 7px;
    border-right: 2px solid #86c332;
    border-left: 2px solid #86c332;
    border-bottom: 2px solid #86c332; }
  #main-nav a:hover, #main-nav a:focus {
    background-color: #60892f;
    color: white;
    box-shadow: inset 1px 1px 5px #23272a; }
  #main-nav #a-welcome {
    color: white;
    font-weight: bold; }

