#projects {
  min-height: 100vh;
  background-color: #60892f;
  background-image: -webkit-repeating-linear-gradient(rgba(134, 195, 50, 0.1), rgba(134, 195, 50, 0.1), transparent 1px, transparent 4px);
  background-image: repeating-linear-gradient(rgba(134, 195, 50, 0.1), rgba(134, 195, 50, 0.1), transparent 1px, transparent 4px);
  padding: 50px 10px 50px 10px; }

.proj-flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center; }
  .proj-flex a {
    text-decoration: none;
    border-radius: 50%; }

.project-tile {
  position: relative;
  width: 100%;
  height: 150px;
  margin: 5px 0;
  padding: 0;
  border: 0;
  display: flex;
  vertical-align: center; }
  .project-tile .project-tile-image {
    background-size: 100% auto;
    background-position: center 25%;
    background-repeat: no-repeat;
    width: 40%;
    height: 100%; }
  .project-tile .project-tile-gap {
    width: 10px; }
  .project-tile .project-tile-info {
    display: inline-block;
    flex-grow: 1;
    height: auto;
    max-width: 55%;
    padding: 10px;
    background-color: #23272a;
    color: #86c332;
    overflow-y: auto;
    scrollbar-width: thin; }
    .project-tile .project-tile-info h3 {
      margin: 0;
      padding: 0; }
    .project-tile .project-tile-info .project-tile-title {
      text-align: center;
      font-weight: bold; }
    .project-tile .project-tile-info .project-description {
      font-size: 12px; }
  .project-tile .project-tile-links {
    width: 40px;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column; }
    .project-tile .project-tile-links .internal-example-link {
      cursor: pointer; }
    .project-tile .project-tile-links a, .project-tile .project-tile-links .internal-example-link {
      display: block;
      border: 1px solid white;
      text-align: center;
      box-sizing: border-box;
      width: 100%;
      max-height: 100%;
      flex-grow: 1;
      color: white;
      background-color: #23272a;
      border-radius: 0; }

@media only screen and (max-width: 700px) {
  .project-tile {
    flex-direction: column;
    height: auto; }
    .project-tile .project-tile-image {
      width: 100%;
      height: 200px; }
    .project-tile .project-tile-gap {
      height: 0px;
      width: 0px; }
    .project-tile .project-tile-info {
      max-width: 100%;
      width: 100%; }
    .project-tile .project-tile-links {
      width: 100%;
      height: 40px;
      flex-wrap: nowrap;
      flex-direction: row; }
      .project-tile .project-tile-links a, .project-tile .project-tile-links .internal-example-link {
        max-width: 100%;
        height: 100%; } }

#welcome-section {
